import { Component, OnInit } from '@angular/core';
import { LooserService } from '../looser.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, interval, Subscription } from 'rxjs';

@Component({
  selector: 'app-toplooser',
  templateUrl: './toplooser.component.html',
  styleUrls: ['./toplooser.component.css']
})
export class ToplooserComponent implements OnInit {

  loosertrades:any=[];
  looserSample: any=[];

  private updateSubscription: Subscription;

  constructor(public rest:LooserService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.updateSubscription = interval(200000).subscribe(
      (val) => { 
        this.getLooserTrades();
    });

    this.getLooserTrades();
  }

  getLooserTrades() {
    this.loosertrades = [];
    this.rest.getLooserTrade().subscribe((data: {}) => {
      console.log(data);
     
      this.looserSample = Array.of(data);
      this.loosertrades=Array.of(this.looserSample)
      
    });
  }
}
