import { Component, OnInit } from '@angular/core';
import { SellService } from '../sell.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, interval, Subscription } from 'rxjs';


@Component({
  selector: 'app-selladvice',
  templateUrl: './selladvice.component.html',
  styleUrls: ['./selladvice.component.css']
})
export class SelladviceComponent implements OnInit {

  selltrades: any=[];
  sellSample: any=[];

  private updateSubscription: Subscription;

  constructor(public rest:SellService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.updateSubscription = interval(200000).subscribe(
      (val) => { 
        this.getSellTrades();
    });

    this.getSellTrades();
  }

  getSellTrades() {
    this.rest.getSellTrade().subscribe((res) => {
      let filtered = [];
     // console.log(res.data.length);
  
        filtered = res.data.filter((item) => {
          return item.open === item.high;
        });
        //console.log(filtered);
        this.selltrades=filtered;
    });
  }

}
