import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {BuyadviceComponent} from './buyadvice/buyadvice.component';
import {SelladviceComponent} from './selladvice/selladvice.component';
import {TopgainerComponent} from './topgainer/topgainer.component';
import {ToplooserComponent} from './toplooser/toplooser.component';

const routes: Routes = [{ path: 'buy', component: BuyadviceComponent },
{ path: 'sell', component: SelladviceComponent },
{ path: 'gainer', component: TopgainerComponent },
{ path: 'looser', component: ToplooserComponent },];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
