import { Component, OnInit } from '@angular/core';
import { BuyService } from '../buy.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, interval, Subscription } from 'rxjs';

@Component({
  selector: 'app-buyadvice',
  templateUrl: './buyadvice.component.html',
  styleUrls: ['./buyadvice.component.css']
})
export class BuyadviceComponent implements OnInit {

  buytrades: any = [];
  buySample: any = [];

  private updateSubscription: Subscription;

  constructor(public rest: BuyService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.updateSubscription = interval(200000).subscribe(
      (val) => {
        this.getBuyTrades();
      });

    this.getBuyTrades();
  }


  getBuyTrades() {
    this.rest.getBuyTrade().subscribe((res) => {
      let filtered = [];
     // console.log(res.data.length);
  
        filtered = res.data.filter((item) => {
          return item.open === item.low;
        });
       // console.log(filtered);
        this.buytrades=filtered;
    });
    }


}
