import { Component, OnInit } from '@angular/core';
import { GainerService } from '../gainer.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable, interval, Subscription } from 'rxjs';


@Component({
  selector: 'app-topgainer',
  templateUrl: './topgainer.component.html',
  styleUrls: ['./topgainer.component.css']
})
export class TopgainerComponent implements OnInit {

  gainerSample: any=[];
  gainertrades: any = [];

  private updateSubscription: Subscription;

  constructor(public rest: GainerService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this.updateSubscription = interval(200000).subscribe(
      (val) => {
        this.getGainerTrades();
      });
    this.getGainerTrades();

  }


  getGainerTrades() {
    this.gainertrades = [];
    this.rest.getGainerTrade().subscribe((data: {}) => {
      console.log(data);
      this.gainerSample = Array.of(data);
      this.gainertrades=Array.of(this.gainerSample)
      console.log(this.gainertrades);


    });
  }

}
